
import { Box } from "@mui/material";
import { memo } from "react";
import styles from "./ErrorDisplay.module.scss"
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import CloseIcon from '@mui/icons-material/Close';
import { isGroup, isQuestion } from "design/utils/designUtils";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { setupOptions } from "constants";
import { serveyRandomSetup } from "constants";
import { setup } from "design/state/designState";


function ErrorDisplay(props) {


    const dispatch = useDispatch()
    const state = useSelector((state) => {
        return state.designState[props.code]
    });

    const errors = state.errors
    const designErrors = state.designErrors
    const instructions = state.instructionList?.filter(instructions => instructions.errors)


    const hasErrors = errors?.length > 0 || designErrors?.length > 0 || instructions?.length > 0

    const type = useSelector((state) => {
        return props.code == "Survey" ? ""
            : isGroup(props.code) ? state.designState[props.code].groupType?.toLowerCase() || "group" : state.designState[props.code].type
    });

    const onErrClick = (instruction) => {
        if (instruction.code === "conditional_relevance") {
            dispatch(setup({ code: props.code, rules: setupOptions[type], highlighted: "relevance", expanded: ["relevance"] }))
        } else if (instruction.code === "random_group" || instruction.code === "priority_groups") {
            if (props.code == "Survey") {
                dispatch(setup({ ...serveyRandomSetup, highlighted: "random" }))
            } else {

                dispatch(setup({ code: props.code, rules: setupOptions[type], highlighted:  "random", expanded: ["random"] }))
            }

        } else if (instruction.code.startsWith("skip_to")) {
            dispatch(setup({ code: props.code, rules: setupOptions[type], highlighted: "skip_logic", expanded: ["skip_logic"] }))
        }
        return ""
    }

    const isClickable = (instruction) => {
        return instruction.code === "conditional_relevance"
            || instruction.code === "random_group" || instruction.code === "priority_groups"
            || instruction.code.startsWith("skip_to")
    }

    return hasErrors
        ? <Box className={styles.errorDisplay}>
            {errors && errors.map((el) => {
                return <div key={el}>
                    <CloseIcon style={{ verticalAlign: "middle" }} />
                    {mapComponentError(props.code, el)}
                </div>
            })}
            {designErrors && designErrors.map((el) => {
                return <div key={el.code}>
                    <CloseIcon style={{ verticalAlign: "middle" }} />
                    {el.message}
                </div>
            })}
            {instructions  && instructions.map((el) => {
                return <div
                    className={isClickable(el) ? styles.clickable : ""}
                    onClick={() => onErrClick(el)}
                    key={el.code}>
                    <ErrorOutlineIcon style={{ verticalAlign: "middle" }} />
                    {mapInstructionError(el)}
                </div>
            })}
        </Box>
        : ""
}


const mapComponentError = (code, error) => {
    if (error === "EMPTY_PARENT") {
        return `This ${componentName(code)} cannot be empty, Please add at least one ${componentChildName(code)}`
    } else if (error === "DUPLICATE_CODE") {
        return `This ${componentName(code)} has a duplicate code`
    } else if (error === "NO_END_GROUP") {
        return `There must be an valid end Group`
    } else if (error === "MISPLACED_END_GROUP") {
        return `End Group must always be the last group`
    } else if (error === "MISPLACED_WELCOME_GROUP") {
        return `Welcome Group must always be the first group`
    }
    return ""
}

const mapInstructionError = (instruction) => {

    if (instruction.code === "value" && instruction.errors[0].name == "ValueInEndGroup") {
        return `Cannot have questions that capture user input in End Group`
    } else if (instruction.code === "conditional_relevance") {
        return `Problem with Show/Hide Logic`
    } else if (instruction.code === "random_group") {
        return "Problem with Random Group instructions"
    } else if (instruction.code === "priority_groups") {
        return "Problem with Priority Group instructions"
    } else if (instruction.code.startsWith("reference")) {
        return <span>Problem piping a value of missing or a following question:
            <b> {instruction.errors.map(error => error.dependency.componentCode).join(", ")}</b>
            , in Language:  <b>{instruction.lang}</b> </span>
    } else if (instruction.code.startsWith("skip_to")) {
        return "Problem with one of the skip instructions"
    }
    return ""
}

const componentName = (code) => {
    if (code == "Survey") {
        return "Survey"
    } else if (isQuestion(code)) {
        return "Question"
    } else if (isGroup(code)) {
        return "Group"
    } else if (code == "Survey") {
        return "Survey"
    }
    return "Answer"
}

const componentChildName = (code) => {
    if (isGroup(code)) {
        return "question"
    } else if (code == "Survey") {
        return "group"
    }
    return "Answer"
}


export default memo(ErrorDisplay)