import { ErrorOutlineOutlined } from "@mui/icons-material";
import { Button, FormControl, MenuItem, Select, Switch } from "@mui/material"
import { Box } from "@mui/material";
import { jumpDestinations } from "design/access/jumpDestinations";
import { editSkipDestination, editSkipToEnd, removeSkipDestination } from "design/state/designState";
import React from "react";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import styles from "./SkipLogic.module.scss";


function SkipLogic({ code }) {
    const dispatch = useDispatch()

    const skipLogic = useSelector((state) => {
        return state.designState[code].skip_logic || {}
    });


    const destinations = useSelector((state) => {
        const mainLang = state.designState.langInfo.mainLang
        return jumpDestinations(state.designState.componentIndex, code, state.designState, mainLang)
    });

    const instructions = useSelector((state) => {
        return state.designState[code].instructionList.filter(el => el.code.startsWith("skip_to"))
    });

    const children = useSelector((state) => {
        const lang = state.designState.langInfo.lang
        return state.designState[code]
            ?.children
            ?.map(child => {
                return { code: child.code, label: state.designState[child.qualifiedCode].content?.label?.[lang] || child.code }
            })
    });

    const onChange = (answerCode, targetCode) => {
        if (targetCode == "proceed") {
            dispatch(removeSkipDestination({ code, answerCode }))
        } else {
            dispatch(editSkipDestination({ code, answerCode, skipTo: targetCode }))
        }
    }

    const onToEndChanged = (answerCode, checked) => {
        dispatch(editSkipToEnd({ code, answerCode, toEnd: checked }))
    }


    return <>
        {children?.map(element => {
            const code = element.code
            const original = skipLogic?.[code]
            const skipTo = original?.skipTo
            const skipToCode = skipTo || "proceed"
            const invalidSkipDestination = skipTo && instructions?.find(el => el.code == "skip_to_on_" + code)?.errors?.find(el => el.name == "InvalidSkipReference")
            return <div key={element.code} className={styles.skipItem} ><span >If answer is <b>{element.label}</b></span>
                {invalidSkipDestination
                    ? skipValueError(code, skipTo, onChange)
                    : skipSelectValue(code, destinations, skipToCode, original?.toEnd, onChange, onToEndChanged)}
            </div>

        })}
    </>

}

function skipValueError(answerCode, skipToCode, onChange) {
    return <Box className={styles.errorDisplay}>
        <ErrorOutlineOutlined style={{ verticalAlign: "middle" }} /> {skipToCode} is not a valid Skip Destination...
        <Button variant="contained" onClick={() => onChange(answerCode, "proceed")}> OK </Button>
    </Box>

}

function skipSelectValue(answerCode, destinations, skipToCode, toEnd, onChange, onToEndChanged,) {
    return <>
        <FormControl variant="standard" fullWidth  >
            <Select
                id="skip-action"
                value={skipToCode}
                label="Skip To"
                onChange={(e) => onChange(answerCode, e.target.value)}>
                <MenuItem key="proceed" value="proceed">Proceed as usual</MenuItem>
                {destinations && destinations?.map(element => {
                    return <MenuItem key={element.code} value={element.code}>{element.label}</MenuItem>
                })}
            </Select>
        </FormControl>
        {skipToCode && skipToCode.startsWith("G") && <div className={styles.toEnd}>
            <h4>To Group End</h4>
            <Switch
                checked={toEnd || false}
                onChange={(event) => onToEndChanged(answerCode, event.target.checked)} />
        </div>}
    </>

}

export default SkipLogic