import React from "react";
import Switch from "@mui/material/Switch";
import { Box } from "@mui/material";
import styles from "./ValidationSetupItem.module.scss";
import ValidationSetupMessage from "design/setup/validation/ValidationSetupMessage"
import ValidationSetupValue from "design/setup/validation/ValidationSetupValue";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { changeValidationValue } from "design/state/designState";


function ValidationSetupItem({
  rule,
  code
}) {

  console.log("ValidationSetupItem: " + code)

  const dispatch = useDispatch()
  const validationRule = useSelector((state) => {
    return state.designState[code]?.validation?.[rule]
  });


  const label = { inputProps: { "aria-label": "Switch validation" } };
  const isActive = validationRule?.isActive || false


  const checkedValidationItem = (checked) => {
    dispatch(changeValidationValue({ code, rule: rule, key: "isActive", value: checked }))
  }

  let title = "";
  switch (rule) {
    case "validation_required":
      title = "Required";
      break;
    case "validation_min_char_length":
      title = "Min Character Length";
      break;
    case "validation_max_char_length":
      title = "Max Character Length";
      break;
    case "validation_contains":
      title = "Contain";
      break;
    case "validation_not_contains":
      title = "Does not contain";
      break;
    case "validation_pattern":
      title = "Regex pattern";
      break;
    case "validation_max_word_count":
      title = "Max Word Count";
      break;
    case "validation_min_word_count":
      title = "Min Word Count";
      break;
    case "validation_pattern_email":
      title = "Pattern Email";
      break;
    case "validation_between":
      title = "Between";
      break;
    case "validation_not_between":
      title = "Not Between";
      break;
    case "validation_lt":
      title = "Less than";
      break;
    case "validation_lte":
      title = "Less than or equal";
      break;
    case "validation_gt":
      title = "Greater than";
      break;
    case "validation_gte":
      title = "Greater than or equal";
      break;
    case "validation_equals":
      title = "Equals";
      break;
    case "validation_not_equal":
      title = "Not equal";
      break;
    case "validation_min_option_count":
      title = "Min option count";
      break;
    case "validation_max_option_count":
      title = "Max option count";
      break;
    case "validation_option_count":
      title = "Exact option count";
      break;
    case "validation_min_ranking_count":
      title = "Min ranked count";
      break;
    case "validation_max_ranking_count":
      title = "Max ranked count";
      break;
    case "validation_ranking_count":
      title = "Exact ranked count";
      break;
    case "validation_option_countt":
      title = "Exact option count";
      break;
    default:
      break;
  }


  return (
    <div>
      <div className={styles.title}>
        <h4>{title}</h4>
        <Switch
          {...label}
          checked={isActive}
          onChange={(event) => checkedValidationItem(event.target.checked)}
        />
      </div>
      {isActive && (
        <Box className={styles.boxContainer}>
          <ValidationSetupValue code={code} validation={validationRule} rule={rule} />
          <ValidationSetupMessage code={code} validationRule={validationRule} rule={rule} />
        </Box>
      )}
    </div>
  );
}

export default React.memo(ValidationSetupItem);
