import React from "react";
import Collapse from "@mui/material/Collapse";
import { Draggable, Droppable } from "react-beautiful-dnd";
import ViewCompactIcon from '@mui/icons-material/ViewCompact';
import QuestionDesign from "design/QuestionDesign";

import styles from "./GroupDesign.module.scss";
import { rtlLanguage } from "common/utils";
import ContentEditor from "design/ContentEditor";
import { Box, Button, Dialog, DialogActions, DialogTitle } from "@mui/material";
import { useTheme } from '@mui/material/styles';
import ErrorDisplay from "design/ErrorDisplay";
import { useDispatch } from "react-redux";
import { collapseAllGroups, deleteGroup, setup, toggleComponentCollapse } from "design/state/designState";
import { useSelector } from "react-redux";
import { setupOptions } from "constants";
import ActionToolbar from "design/ActionToolbar";

function GroupDesign(props) {
  console.log(props.code)

  const dispatch = useDispatch()
  const [open, setOpen] = React.useState(false);

  const collapsed = useSelector((state) => {
    return state.designState[props.code].collapsed
  });


  const isInSetup = useSelector((state) => {
    return state.designState.setup?.code == props.code
  });

  const children = useSelector((state) => {
    return state.designState[props.code].children
  });


  const langInfo = useSelector((state) => {
    return state.designState.langInfo
  });

  const toggleCollapse = () => {
    dispatch(toggleComponentCollapse(props.code))
  }


  const setSetup = React.useCallback(() => { dispatch(setup({ code: props.code, rules: setupOptions["group"] })) })



  const theme = useTheme()


  return (
    <React.Fragment>
      <Draggable
        isDragDisabled={props.isDragDisabled}
        draggableId={props.code}
        index={props.dragIndex}
        key={props.code}>
        {(provided) => (
          <div
            ref={provided.innerRef}
            {...provided.draggableProps}
          >
            <Box className={styles.groupCard}
              dir={rtlLanguage.includes(langInfo.lang) ? 'rtl' : 'ltr'}
              sx={{ borderColor: 'grey.500', boxShadow: 1, backgroundColor: isInSetup ? 'beige' : 'background.paper' }}
            >
              <Box sx={{ backgroundColor: 'primary.main', height: '8px' }} className={styles.groupHeader}></Box>

              <div {...provided.dragHandleProps} className={styles.moveBox} onMouseDown={(event) => { if (props.isDragDisabled) { event.preventDefault(); setOpen(true) } }}>
                <ViewCompactIcon />
              </div>
              <ActionToolbar
                code={props.code}
                isGroup={true}
                toggleCollapse={toggleCollapse}
                collapsed={collapsed}
                onDelete={() => dispatch(deleteGroup(props.code))}
                disableDelete={children && children.length > 0}
              />
              <div className={styles.titleRow}>
                <div className={styles.textHeader}
                  style={{ fontFamily: theme.textStyles.group.font, color: theme.textStyles.group.color, fontSize: theme.textStyles.group.size }}>
                  <ContentEditor
                    code={props.code}
                    extended={false}
                    onActive={setSetup}
                    contentKey="label"
                    placeholder="Title"
                  />
                </div>
                <Box className={styles.textDescription}>
                  <ContentEditor
                    code={props.code}
                    extended={true}
                    onActive={setSetup}
                    contentKey="description"
                    placeholder="Description"
                  />
                </Box>

              </div>
              {<ErrorDisplay type="group" code={props.code} />}
            </Box>
          </div>
        )}
      </Draggable>

      <Collapse
        className={styles.questionsOuterContainer}
        dir={rtlLanguage.includes(langInfo.lang) ? 'rtl' : ''}
        in={collapsed !== true}
        sx={{ backgroundColor: isInSetup ? 'beige' : 'background.paper' }}
        timeout="auto"
        unmountOnExit
      >
        <Droppable droppableId={props.code} type="questions">
          {(provided) => (
            <Box
              ref={provided.innerRef} {...provided.droppableProps}
              className={styles.questionsInnerContainer}
              sx={{ backgroundColor: 'background.default' }}>
              {children?.map((quest, index) => {
                return <Draggable
                  parentIndex={props.dragIndex}
                  key={quest.code}
                  draggableId={quest.code}
                  index={props.dragIndex + index + 1}
                >
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      snapshot={snapshot}
                      {...provided.draggableProps}
                    >
                      <QuestionDesign
                        dragHandleProps={provided.dragHandleProps}
                        key={quest.code}
                        parentCode={props.code}
                        index={index}
                        type={quest.type}
                        code={quest.code}
                      />
                    </div>
                  )}
                </Draggable>
              })}
              {provided.placeholder}
            </Box>
          )}
        </Droppable>
      </Collapse>

      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        aria-labelledby="alert-dialog-title-collapse-all"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title-collapse-all">
          {"All Groups Must be collapsed first before you can drag them... Collapse All?"}
        </DialogTitle>
        <DialogActions>
          <Button onClick={() => setOpen(false)} >
            Cancel
          </Button>
          <Button onClick={() => { dispatch(collapseAllGroups()); setOpen(false) }}>
            Collapse
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}

export default React.memo(GroupDesign);
