import React from "react";
import { FormControl, MenuItem, Select } from "@mui/material";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { changeAttribute } from "design/state/designState";
import CustomPriority from "../CustomPriority";
import styles from "./OrderPrioritySetup.module.scss";
import CustomRandom from "../CustomRandom";


export default function OrderPrioritySetup({
  rule,
  code
}) {


  const dispatch = useDispatch()

  const value = useSelector((state) => {
    return state.designState[code][rule] || 'NONE'
  });



  let title = ""
  let labels = []
  let values = []

  switch (rule) {
    case "randomize_questions":
      title = "Questions Order"
      labels = ["As is", "Random Order", "Flip Order", "Custom"]
      values = ["NONE", "RANDOM", "FLIP", "custom"]
      break;
    case "prioritize_questions":
      title = "Questions Priority"
      labels = ["None", "Prioritise Questions"]
      values = ["NONE", "prioritize"]
      break;
    case "randomize_groups":
      title = "Groups Order"
      labels = ["As is", "Random Order", "Flip Order", "Custom"]
      values = ["NONE", "RANDOM", "FLIP", "custom"]
      break;
    case "prioritize_groups":
      title = "Groups Priority"
      labels = ["None", "Prioritise Groups"]
      values = ["NONE", "prioritize"]
      break;
    case "randomize_options":
      title = "Options Order"
      labels = ["As is", "Random Order", "Flip Order", "Sort By Label", "Custom"]
      values = ["NONE", "RANDOM", "FLIP", "ALPHA", "custom"]
      break;
    case "prioritize_options":
      title = "Options Priority"
      labels = ["None", "Prioritise Options"]
      values = ["NONE", "prioritize"]
      break;
    case "randomize_rows":
      title = "Rows Order"
      labels = ["As is", "Random Order", "Flip Order", "Sort By Label", "Custom"]
      values = ["NONE", "RANDOM", "FLIP", "ALPHA", "custom"]
      break;
    case "prioritize_rows":
      title = "Rows Priority"
      labels = ["None", "Prioritise Rows"]
      values = ["NONE", "prioritize"]
      break;
    case "randomize_columns":
      title = "Columns Order"
      labels = ["As is", "Random Order", "Flip Order", "Sort By Label", "Custom"]
      values = ["NONE", "RANDOM", "FLIP", "ALPHA", "custom"]
      break;
    case "prioritize_columns":
      title = "Columns Priority"
      labels = ["None", "Prioritise Columns"]
      values = ["NONE", "prioritize"]
      break;
  }

  const onChange = (value) => {
    const finalValue = value == "NONE" ? undefined : value
    dispatch(changeAttribute({ code, key: rule, value: finalValue }))
  }


  return <>
    <h4>{title}</h4>
    <FormControl className={styles.selectValue} variant="standard" fullWidth  >
      <Select
        id="select-value"
        value={value}
        label="Select Value"
        onChange={(e) => { onChange(e.target.value) }}
      >
        {(labels).map((element, index) => {
          return <MenuItem key={element} value={values[index]}>{element}</MenuItem>
        })}
      </Select>
    </FormControl>
    {value == "custom" && ["randomize_questions", "randomize_options", "randomize_groups"].indexOf(rule) !== -1 && <CustomRandom
      label=""
      code={code}
    />}
    {value == "custom" && "randomize_rows" == rule && <CustomRandom
      label=""
      type="row"
      code={code}
    />}
    {value == "prioritize" && "prioritize_rows" == rule && <CustomPriority
      label=""
      type="row"
      code={code}
    />}
    {value == "custom" && "randomize_columns" == rule && <CustomRandom
      label=""
      type="column"
      code={code}
    />}
    {value == "prioritize" && "prioritize_columns" == rule && <CustomPriority
      label=""
      type="column"
      code={code}
    />}
    {value == "prioritize" && ["prioritize_questions", "prioritize_options", "prioritize_groups"].indexOf(rule) !== -1 && <CustomPriority
      label=""
      code={code}
    />
    }
  </>

}
