import React from "react";
import ViewCompactIcon from '@mui/icons-material/ViewCompact';

import TextQuestionDesign from "design/TextQuestionDesign";
import EmailQuestionDesign from "design/EmailQuestionDesign";
import NumberQuestionDesign from "design/NumberQuestionDesign";
import ParagraphQuestionDesign from "design/ParagraphQuestionDesign";

import styles from "./QuestionDesign.module.scss";
import { nextId } from "common/utils";
import ContentEditor from "design/ContentEditor";
import FileUploadQuestionDesign from "design/FileUploadQuestionDesign";
import DateTimeQuestionDesign from "design/DateTimeQuestionDesign";
import TimeQuestionDesign from "design/TimeQuestionDesign";
import ChoiceQuestion from "design/ChoiceQuestion"
import SCQArray from "design/SCQArray"
import { Box, Collapse } from "@mui/material";
import { useTheme } from '@mui/material/styles';
import ErrorDisplay from "design/ErrorDisplay";
import { useSelector } from "react-redux";
import { addNewAnswer, changeAttribute, cloneQuestion, deleteQuestion, setup, toggleComponentCollapse } from "design/state/designState";
import { useDispatch } from "react-redux";
import { setupOptions } from "constants";
import ActionToolbar from "design/ActionToolbar";
import ImageChoiceQuestion from "design/ImageChoiceQuestion";
import VideoDisplayDesign from "design/VideoDisplayDesign";
import SignatureDesign from "design/SignatureDesign";
import ImageDisplayDesign from "design/ImageDisplayDesign";
import NPSDesign from "design/NPSDesign";

function QuestionDesign({ code, type, parentCode, dragHandleProps }) {
  console.log(code)
  const dispatch = useDispatch()
  const theme = useTheme()

  const isInSetup = useSelector((state) => {
    return state.designState.setup?.code == code
  });

  const children = useSelector((state) => {
    return state.designState[code].children
  });

  const collapsed = useSelector((state) => {
    return state.designState[code].collapsed
  });

  const toggleCollapse = () => {
    dispatch(toggleComponentCollapse(code))
  }



  const showQuestion = () => {
    switch (type) {
      case "video_display":
        return (
          <VideoDisplayDesign
            key={code}
            code={code}
          />
        );
      case "image_display":
        return (
          <ImageDisplayDesign
            key={code}
            code={code}
          />
        );
      case "signature":
        return <SignatureDesign />
      case "date_time":
        return (
          <DateTimeQuestionDesign
            key={code}
            code={code}
          />
        );
      case "date":
        return (
          <DateTimeQuestionDesign
            key={code}
            code={code}
          />
        );
      case "time":
        return (
          <TimeQuestionDesign
            key={code}
            code={code}
          />
        );
      case "scq":
        return (
          <ChoiceQuestion
            key={code}
            addNewAnswer={addAnswer}
            code={code}
            type="radio"
          />
        );
      case "image_mcq":
      case "image_scq":
      case "image_ranking":  
        return (
          <ImageChoiceQuestion
            key={code}
            addNewAnswer={addAnswer}
            code={code}
          />
        );
      case "scq_array":
        return (
          <SCQArray
            key={code}
            addNewAnswer={addAnswer}
            code={code}
          />
        );
      case "file_upload":
        return (
          <FileUploadQuestionDesign
            key={code}
            code={code}
          />
        );
      case "mcq":
        return (
          <ChoiceQuestion
            key={code}
            addNewAnswer={addAnswer}
            code={code}
            type="checkbox"
          />
        );
      case "ranking":
        return (
          <ChoiceQuestion
            key={code}
            addNewAnswer={addAnswer}
            code={code}
            type="ranking"
          />
        );
        case "nps":
        return (
          <NPSDesign
            key={code}
            code={code}
          />
        );
      case "number":
        return (
          <NumberQuestionDesign
            key={code}
            code={code}
          />
        );
      case "text":
        return (
          <TextQuestionDesign
            key={code}
            code={code}
          />
        );
      case "paragraph":
        return (
          <ParagraphQuestionDesign
            key={code}
            code={code}
          />
        );
      case "email":
        return (
          <EmailQuestionDesign
            key={code}
            code={code}
          />
        );
      default:
        return "";
    }
  };

  const addAnswer = (questionCode, questionType, type) => {
    const answers = children || []
    let nextAnswerIndex = 1
    let code = ''
    let qualifiedCode = ''
    let label = ''

    const valueInstruction = {
      "code": "value",
      "isActive": false,
      "returnType": {
        "name": questionType == "ranking" || questionType == "nps" || questionType == "image_ranking" ? "Int" : "Boolean"
      },
      "text": ""
    }

    switch (type) {
      case 'column':
        nextAnswerIndex = nextId(answers.filter(el => el.type === "column"))
        label = 'Col' + nextAnswerIndex
        code = 'Ac' + nextAnswerIndex
        qualifiedCode = questionCode + code
        dispatch(addNewAnswer({ label, answer: { code, qualifiedCode, type } }))
        break;
      case 'row':
        nextAnswerIndex = nextId(answers.filter(el => el.type === "row"))
        code = 'A' + nextAnswerIndex
        label = 'Row' + nextAnswerIndex
        qualifiedCode = questionCode + code
        dispatch(addNewAnswer({ label, instructionList: [valueInstruction], answer: { code, qualifiedCode, type } }))
        break;
      case 'other':
        code = 'Aother'
        label = 'Other'
        qualifiedCode = questionCode + code
        const instructionListForText = [
          {
            "code": "value",
            "isActive": false,
            "returnType": {
              "name": "String"
            },
            "text": ""
          },
          {
            "code": "conditional_relevance",
            "isActive": true,
            "returnType": {
              "name": "Boolean"
            },
            "text": questionType === 'scq' ? `${questionCode}.value === 'Aother'` : `${questionCode}Aother.value === true`
          }
        ]
        dispatch(addNewAnswer({ label, answer: { code, qualifiedCode, type }, instructionList: questionType == "mcq" ? [valueInstruction] : [] }))
        dispatch(addNewAnswer({ instructionList: instructionListForText, answer: { code: "Atext", qualifiedCode: qualifiedCode + "Atext", type: "other_text" } }))
        break;
      default:
        nextAnswerIndex = nextId(answers)
        code = 'A' + nextAnswerIndex
        label = 'Option' + nextAnswerIndex
        qualifiedCode = questionCode + code
        dispatch(addNewAnswer({ label, answer: { code, qualifiedCode }, instructionList: (questionType == "mcq" || questionType == "image_mcq" || questionType == "ranking" || questionType == "image_ranking") ? [valueInstruction] : [] }))
        break;
    }

  }


  const setSetup = React.useCallback(() => { dispatch(setup({ code, rules: setupOptions[type] })) })

  return <Box sx={{ backgroundColor: isInSetup ? 'beige' : 'background.paper', borderColor: 'grey.500' }}
    className={`${styles.groupQuestion}`}
  >
    <div className={styles.questionWrapper}>
      <>
        <div className={styles.moveBox} {...dragHandleProps}>
          <ViewCompactIcon />
        </div>

        <ActionToolbar
          isGroup={false}
          code={code}
          parentCode={parentCode}
          toggleCollapse={toggleCollapse}
          collapsed={collapsed}
          onClone={() => dispatch(cloneQuestion(code))}
          onDelete={() => dispatch(deleteQuestion(code))}
          disableDelete={false}
        />

      </>

      <div className={styles.titleQuestion}
        style={{ fontFamily: theme.textStyles.question.font, color: theme.textStyles.question.color, fontSize: theme.textStyles.question.size }}>
        <ContentEditor
          code={code}
          extended={false}
          onActive={setSetup}
          placeholder="Title"
          contentKey="label"
        />

      </div>
      <Box className={styles.textDescriptionContent}>
        <ContentEditor
          code={code}
          onActive={setSetup}
          extended={true}
          placeholder="Description"
          contentKey="description"
        />
      </Box>

    </div>

    <Collapse
      in={collapsed !== true}
      timeout="auto"
      unmountOnExit
    >

      {showQuestion()}
    </Collapse>
    <ErrorDisplay code={code} />
  </Box>;
}

export default React.memo(QuestionDesign);
