import { FormControl, MenuItem, Select, Button } from "@mui/material"
import LogicBuilder from "design/setup/logic/LogicBuilder"
import { changeRelevance } from "design/state/designState";
import React, { useState } from "react"
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import { buildFields } from "../LogicBuilder/buildFields";
import styles from "./Relevance.module.scss";

function Relevance({ code }) {
    const dispatch = useDispatch()

    const [logicDialogOpen, setLogicDialogOpen] = useState(false)
    const mainLang = useSelector((state) => {
        return state.designState.langInfo.mainLang
    });
    const state = useSelector((state) => {
        return state.designState[code]
    });
    const fields = useSelector((state) => {
        console.log(state.designState)
        return buildFields(state.designState.componentIndex, code, state.designState, mainLang)
    });

    const logicDisabled = !hasErrors && Object.keys(fields).length > 0
    const instruction = state.instructionList?.find((instruction) => instruction.code == "conditional_relevance")
    const errors = instruction?.errors || []
    const hasErrors = errors.length > 0
    const logic = state.relevance?.logic

    const [rule, setRule] = useState(state.relevance?.rule || "show_always")
    const shouldHaveLogic = (rule == "show_if" || rule == "hide_if")



    const onRuleChange = (rule) => {
        setRule(rule);
        switch (rule) {
            case 'show_always':
                reset()
                return
            case 'hide_always':
                dispatch(changeRelevance({ code, key: "relevance", value: { logic: undefined, "rule": rule } }))
                return
            case 'show_if':
            case 'hide_if':
                if (logic?.rule != "show_if" && logic?.rule != "hide_if") {
                    setLogicDialogOpen(true)
                }
                if (logic) {
                    dispatch(changeRelevance({ code, key: "relevance", value: { logic: logic, "rule": rule } }))
                }
                return
            default:
                break;
        }
    }

    const reset = () => dispatch(changeRelevance({ code, key: "relevance", value: { logic: undefined, "rule": "show_always" } }))

    const onLogicChange = (logic) => {
        setLogicDialogOpen(false)
        if (shouldHaveLogic) {
            dispatch(changeRelevance({ code, key: "relevance", value: { "logic": logic, "rule": rule } }))
        }

    }

    return <div className={`${hasErrors ? styles.relevanceError : ""}`}>
        <FormControl variant="standard" fullWidth>
            <Select
                id="show-hide-select"
                value={rule}
                label="Age"
                onChange={(e) => onRuleChange(e.target.value)}>
                <MenuItem disabled={hasErrors} key="show_always" value="show_always">Show Always</MenuItem>
                <MenuItem disabled={!logicDisabled} key="show_if" value="show_if">Show If</MenuItem>
                <MenuItem disabled={!logicDisabled} key="hide_if" value="hide_if">Hide If</MenuItem>
                <MenuItem disabled={hasErrors} key="hide_always" value="hide_always">Hide Always</MenuItem>
            </Select>
        </FormControl>
        {!hasErrors && shouldHaveLogic &&
            <LogicBuilder
                title={rule == "show_if" ? "Condition To Show" : "Condition To Hide"}
                onChange={onLogicChange}
                onDialogStateChanged={(state) => setLogicDialogOpen(state)}
                fields={fields}
                dialogOpen={logicDialogOpen}
                logic={logic} />

        }

        {
            hasErrors
                ? <div className={styles.errorContainer}>
                    <span> There was an error in Logic therefore this was reset to <b>Always Show</b></span>
                    <Button variant="contained" onClick={() => reset()}>
                        OK
                    </Button>
                </div>
                : ""

        }
        {
            (shouldHaveLogic && !logic)
                ? <div>
                     Without Logic, this will be reset to <b>Always Show</b>
                </div>
                : ""

        }
    </div>

}
export default Relevance
