import { PreviewOutlined } from "@mui/icons-material"
import { Box, IconButton, MenuItem, Select } from "@mui/material"
import { useDispatch } from "react-redux"
import PaletteIcon from '@mui/icons-material/Palette';
import styles from "./LeftPanel.module.scss";
import BuildIcon from '@mui/icons-material/Build';
import SetupComponentsPanel from "design/setup/SetupPanel";
import CloseIcon from "@mui/icons-material/Close";
import { toPreview } from "design/GetData";

import NewComponentsPanel from "design/NewComponentsPanel";
import { useSelector } from "react-redux";
import { changeLang, resetSetup, setup } from "design/state/designState";
import { serveyRandomSetup } from "constants";

function LeftPanel() {
    const dispatch = useDispatch()

    const setupInfo = useSelector((state) => {
        return state.designState.setup
    });


    const langInfo = useSelector((state) => {
        return state.designState.langInfo
    });

    const lang = langInfo?.lang
    const mainLang = langInfo?.mainLang
    const languagesList = langInfo?.languagesList


    return <Box sx={{ backgroundColor: 'background.paper' }} className={styles.leftContent}>
        <div className={styles.leftPanelGroupItem}>
            <Select
                id="ChangeLang"
                value={lang}
                onChange={(event) => {
                    dispatch(changeLang(event.target.value))
                }}
            >
                {languagesList ? languagesList.map((lang, index) => <MenuItem key={index} value={lang.code}>{lang.langName}</MenuItem>) : ""}
            </Select>
            <div>
                <IconButton onClick={() => toPreview(lang)}>
                    <PreviewOutlined className={styles.settingIcon} />
                </IconButton>
            </div>
            {lang === mainLang ?
                <div>
                    <IconButton onClick={() => dispatch(setup({ code: 'Survey', rules: [{ title: "Theme", rules: ["theme"] }] }))}>
                        <PaletteIcon className={styles.settingIcon} />
                    </IconButton>
                    <IconButton onClick={() => dispatch(setup(serveyRandomSetup))}>
                        <BuildIcon className={styles.settingIcon} />
                    </IconButton>
                </div> : null
            }
        </div>
        {<>
            {setupInfo ? (
                <>
                    <div className={styles.close}>
                        <IconButton onClick={() => dispatch(resetSetup())}>
                            <CloseIcon />
                        </IconButton>
                    </div>
                    <SetupComponentsPanel
                        key={setupInfo.code}
                        setupInfo={setupInfo}
                    />
                </>
            ) : (
                <NewComponentsPanel />
            )}
        </>}
    </Box>

}

export default LeftPanel