import React from "react";

import { useTheme } from '@mui/material/styles';
import { useSelector } from "react-redux";
import ReactPlayer from "react-player";
import { Button } from "@mui/material";
import VideocamIcon from '@mui/icons-material/Videocam';
import { buildResourceUrl, uploadResourceFile } from "design/GetData";
import { useDispatch } from "react-redux";
import { changeAttribute } from "design/state/designState";
import styles from "./VideoDisplay.module.scss";


function VideoDisplayDesign({ code }) {
  const theme = useTheme()
  const dispatch = useDispatch()

  const state = useSelector((state) => {
    return state.designState[code]
  });

  const handleVideoUpload = (e) => {
    e.preventDefault()
    let file = e.target.files[0]
    uploadResourceFile(file).then((response) => {
      dispatch(changeAttribute({ code, key: "videoUrl", value: file.name }))
    }).catch((err) => {
      console.error(err)
    });
  }

  return (
    <>
      {state.videoUrl && <div style={{
        position: "relative",
        marginTop: "16px",
        // 16:9 aspect ratio
        paddingTop: state.audio_only ? "10%" : "56%"
      }}><ReactPlayer
          url={buildResourceUrl(state.videoUrl)}
          loop={state.loop || false}
          light={true}
          controls={true}
          config={{
            forceAudio: state.audio_only || false
          }}
          style={{
            backgroundColor: "black",
            position: "absolute",
            top: "0",
            left: "0",
          }}
          volume={1}
          width="100%"
          height="100%" /></div>}

      <div className={styles.buttonContainer}>
        <Button component="label" variant="outlined" startIcon={<VideocamIcon />}>
          {state.video ? "Replace Video" : "Upload Video"}
          <input
            hidden
            id={code}
            accept="video/*"
            multiple
            type="file"
            onChange={handleVideoUpload} />
        </Button>
      </div>
    </>
  );
}

export default VideoDisplayDesign;
