
import React from "react";
import GroupDesign from "design/GroupDesign";
import { Droppable } from "react-beautiful-dnd";
import styles from "./ContentPanel.module.scss";
import { useTheme } from '@mui/material/styles';
import { useSelector } from "react-redux";
import { buildIndex } from "design/state/designState";
import { buildResourceUrl } from "design/GetData";
import { CardMedia } from "@mui/material";
import { isEquivalent } from "common/utils";
import ErrorDisplay from "design/ErrorDisplay";


function ContentPanel(props) {
  const theme = useTheme()

  let isDragDisabled = false

  const codeIndex = useSelector((state) => {
    return buildIndex(state.designState, props.groups)
  }, isEquivalent);

  const collapseInfo = useSelector((state) => {
    const info = {}
    props.groups?.forEach(element => {
      info[element.code] = state.designState[element.code].collapsed
    })
    return info
  }, isEquivalent);


  props.groups?.forEach(group => {
    if (group && !collapseInfo[group.code]) {
      isDragDisabled = true
    }
  })

  return <div className={styles.dropPanel} style={{ fontFamily: theme.textStyles.text.font, color: theme.textStyles.text.color, fontSize: theme.textStyles.text.size }}>
    <ErrorDisplay code="Survey" />
    <Droppable droppableId="content-panel" type="groups">
      {(provided) => (
        <div className={styles.droppable} ref={provided.innerRef} {...provided.droppableProps}>
          {props.headerImage && <CardMedia
            className={styles.cardImage}
            component="img"
            image={buildResourceUrl(props.headerImage)}
            height="140"
          />}
          {props.groups && props.groups.map((group) => {
            const groupDesign = <GroupDesign className={styles.surveyGroups}
              key={group.code}
              code={group.code}
              dragIndex={codeIndex.indexOf(group.code)}
              isDragDisabled={isDragDisabled}
            />
            return groupDesign
          }
          )}
          {provided.placeholder}

          <div className={styles.footer} />
        </div>
      )}
    </Droppable>
  </div>
}

export default ContentPanel;
