import { TextField } from "@mui/material";
import styles from "./ValidationSetupValue.module.scss";
import { useDispatch } from "react-redux";
import { changeValidationValue } from "design/state/designState";
import { useSelector } from "react-redux";


function ValidationSetupValue({
    code,
    validation,
    rule,
}) {

    const bounds = useSelector((state) => {
        switch (rule) {
            case "validation_min_char_length":
                return [1, Number.MAX_VALUE]
            case "validation_max_char_length":
                return [1, Number.MAX_VALUE]
            case "validation_max_word_count":
                return [1, Number.MAX_VALUE]
            case "validation_min_word_count":
                return [1, Number.MAX_VALUE]
            case "validation_min_ranking_count":
            case "validation_min_option_count":
                return [1, state.designState[code].children?.length || 0]
            case "validation_max_ranking_count":
            case "validation_max_option_count":
                return [1, state.designState[code].children?.length || 0]
            case "validation_ranking_count":
            case "validation_option_count":
                return [1, state.designState[code].children?.length || 0]
            default:
                return undefined;
        }
    });

    const dispatch = useDispatch()

    const onChange = (key, value) => {
        dispatch(changeValidationValue({ rule, code, key, value }))
    }

    const onValuesUpdate = (key, value) => {
        onChange(key, typeof bounds === "undefined" ? value : Math.max(bounds[0], Math.min(bounds[1], value)))
    }

    let keys = validationAttributes(validation)

    let subTitle = "";
    switch (rule) {
        case "validation_min_char_length":
            subTitle = "Min Character:";
            break;
        case "validation_max_char_length":
            subTitle = "Max Character:";
            break;
        case "validation_contains":
            subTitle = "Contain:";
            break;
        case "validation_not_contains":
            subTitle = "Does not contain any of:";
            break;
        case "validation_pattern":
            subTitle = `Matches regex pattern:`;
            break;
        case "validation_max_word_count":
            subTitle = "Max Word Count:";
            break;
        case "validation_min_word_count":
            subTitle = "Min Word Count:";
            break;
        case "validation_between":
            subTitle = "Between:";
            break;
        case "validation_not_between":
            subTitle = "Not Between:";
            break;
        case "validation_lt":
            subTitle = "Less than:";
            break;
        case "validation_lte":
            subTitle = "Less than or equal:";
            break;
        case "validation_gt":
            subTitle = "Greater than:";
            break;
        case "validation_gte":
            subTitle = "Greater than or equal:";
            break;
        case "validation_equals":
            subTitle = "Equals:";
            break;
        case "validation_not_equal":
            subTitle = "Not equal:";
            break;
        case "min_count":
            subTitle = "Min count";
            break;
        case "max_count":
            subTitle = "Max count";
            break;
        case "exact_count":
            subTitle = "Exact count";
            break;
        case "required_other_answer":
            subTitle = "Required answer for other";
            break;
        default:
            break;
    }


    return <div className={styles.valueValidationItemsContainer}>
        {keys && <div>{subTitle}</div>}
        <div className={styles.valueValidationItems}>
            {keys.map((i) => {
                const isInError = typeof bounds !== "undefined" && (validation[i] < bounds[0] || validation[i] > bounds[1])
                return <TextField
                    key={i}
                    error={isInError}
                    value={validation[i]}
                    variant="outlined"
                    size="small"
                    type={
                        typeof validation[i] === "number" ? "number" : "text"
                    }
                    onChange={(event) =>
                        onValuesUpdate(i, typeof validation[i] === "number" ? parseInt(event.target.value) : event.target.value)
                    }
                    style={{ maxWidth: "150px", marginLeft: subTitle ? "30px" : '0' }}
                />
            })}
        </div>
    </div>

}

const validationAttributes = (validation) => {
    return Object.keys(validation)
        .filter(el => !["content", "isActive", "isCustomErrorActive", "bounds"].includes(el))
}


export default ValidationSetupValue

