import { Button, Typography } from "@mui/material"
import styles from "./Theming.module.scss"
import ThemingItem from "./ThemingItem"
import ImageIcon from '@mui/icons-material/Image'
import ColorPicker from "./ColorPicker"
import { useSelector } from "react-redux"
import { useDispatch } from "react-redux"
import { changeAttribute } from "design/state/designState"
import { uploadResourceFile } from "design/GetData"
import { defaultSurveyTheme } from "constants"

function Theming() {
	const dispatch = useDispatch()


	const theme = useSelector((state) => {
		return state.designState.Survey.theme
	});

	function handleChange(key, val) {
		dispatch(changeAttribute({ code: "Survey", key: "theme", value: { ...theme, [key]: val } }))
	}



	function handleImageChange(e) {
		e.preventDefault()
		let file = e.target.files[0]
		uploadResourceFile(file).then((response) => {
			dispatch(changeAttribute({ code: "Survey", key: "headerImage", value:  file.name }))
		}).catch((err) => {
			console.error(err)
		});
	}

	return <div className={styles.theming}>
		<h5>Text Style</h5>

		<h6>
			Group Title
		</h6>

		<ThemingItem
			key="group"
			value={theme.textStyles.group}
			default={defaultSurveyTheme.textStyles.group}
			onChange={val => { handleChange('textStyles', { ...theme.textStyles, ["group"]: val }) }} />
		<hr />
		<h6>
			Question Title
		</h6>

		<ThemingItem
			key="question"
			value={theme.textStyles.question}
			default={defaultSurveyTheme.textStyles.question}
			onChange={val => handleChange('textStyles', { ...theme.textStyles, ["question"]: val })} />
		<hr />
		<h6>
			Text
		</h6>
		<ThemingItem
			key="text"
			value={theme.textStyles.text}
			default={defaultSurveyTheme.textStyles.text}
			onChange={val => handleChange('textStyles', { ...theme.textStyles, ["text"]: val })} />
		<hr />
		<h5>
			Highlight Color
		</h5>
		<ColorPicker color={theme.primaryColor} default={defaultSurveyTheme.primaryColor} handleChange={value => handleChange('primaryColor', value)} />

		<hr />
		<h5>
			Background Color
		</h5>
		<ColorPicker color={theme.bgColor} default={defaultSurveyTheme.bgColor} handleChange={value => handleChange('bgColor', value)} />
		<hr />
		<h5>
			Foreground Color
		</h5>
		<ColorPicker color={theme.paperColor} default={defaultSurveyTheme.paperColor} handleChange={value => handleChange('paperColor', value)} />
		<hr />
		<h5>
			Header
		</h5>
		<Button
			variant="outlined"
			component="label">
			<ImageIcon className="mr-10" />
			Choose Image
			<input
				hidden
				accept="image/*"
				multiple
				type="file"
				onChange={handleImageChange} />
		</Button>
	</div>
}

export default Theming
