import React from "react";

import { useSelector } from "react-redux";
import { Button } from "@mui/material";
import PhotoIcon from '@mui/icons-material/Photo';
import { buildResourceUrl, uploadResourceFile } from "design/GetData";
import { useDispatch } from "react-redux";
import { changeAttribute } from "design/state/designState";
import styles from "./ImageDisplayDesign.module.scss";


function ImageDisplayDesign({ code }) {
  const dispatch = useDispatch()

  const state = useSelector((state) => {
    return state.designState[code]
  });


  const handleUpload = (e) => {
    e.preventDefault()
    let file = e.target.files[0]
    uploadResourceFile(file).then((response) => {
      dispatch(changeAttribute({ code, key: "imageUrl", value: file.name }))
    }).catch((err) => {
      console.error(err)
    });
  }

  return (
    <>
      {state.imageUrl && <img
      style={{
        width:"100%"
      }}
        src={buildResourceUrl(state.imageUrl)}
      />}

      <div className={styles.buttonContainer}>
        <Button component="label" variant="outlined" startIcon={<PhotoIcon />}>
          {state.video ? "Replace Image" : "Upload Image"}
          <input
            hidden
            id={code}
            accept="image/*"
            type="file"
            onChange={handleUpload} />
        </Button>
      </div>
    </>
  );
}

export default ImageDisplayDesign;
