import styles from "./SCQArray.module.scss"
import { Droppable } from "react-beautiful-dnd"
import { Button, useTheme } from "@mui/material"
import ChoiceQuestionItem from "design/ChoiceQuestionItem"
import { useSelector } from "react-redux"

function SCQArray(props) {
  const theme = useTheme()


  const rows = useSelector((state) => {
    return state.designState[props.code].children?.filter(el => el.type == "row") || []
  });


  const columns = useSelector((state) => {
    return state.designState[props.code].children?.filter(el => el.type == "column") || []
  });


  return (
    <div className={styles.scqList}>
      <Droppable droppableId={`row-${props.code}`} type={`row-${props.code}`}>
        {(provided) => (
          <div
            ref={provided.innerRef}
            {...provided.droppableProps}
            className={styles.scqItem}>
            <h5 >Rows</h5>
            {rows && rows.map((item, index) => {
              return <ChoiceQuestionItem
                key={item.code}
                code={item.code}
                index={index}
                qualifiedCode={item.qualifiedCode}
                type="numberOrder" />
            })}
            {provided.placeholder}
            <div className={styles.answerAdd} >
              <Button
                sx={{ fontFamily: theme.textStyles.text.font, fontSize: theme.textStyles.text.size }}
                onClick={e => props.addNewAnswer(props.code, props.type, 'row')}>
                Add row
              </Button>
            </div>
          </div>
        )}
      </Droppable>
      <Droppable droppableId={`col-${props.code}`} type={`col-${props.code}`}>
        {(provided) => (
          <div
            ref={provided.innerRef}
            {...provided.droppableProps}
            className={styles.scqItem}>
            <h5>Columns</h5>
            {columns && columns.map((item, index) => {
              return <ChoiceQuestionItem
                key={item.code}
                code={item.code}
                index={index}
                qualifiedCode={item.qualifiedCode}
                type="radio"
              ></ChoiceQuestionItem>

            })}
            {provided.placeholder}
            <div className={styles.answerAdd}>
              <Button
                sx={{ fontFamily: theme.textStyles.text.font, fontSize: theme.textStyles.text.size }}
                size="small"
                onClick={e => props.addNewAnswer(props.code, props.type, 'column')}>
                Add Column
              </Button>
            </div>
          </div>
        )}
      </Droppable>
    </div>
  )
}

export default SCQArray
