import React, { useEffect } from "react";
import { DragDropContext } from "react-beautiful-dnd";
import { Button, ThemeProvider, createTheme } from "@mui/material";

import Layout from "components/Layout";
import styles from "./EditSurvey.module.scss";
import ContentPanel from "design/ContentPanel";
import { onDragEnd } from "design/DragBehavior";

import { GetData, ResetData } from "design/GetData";
import { defualtTheme } from "constants";
import { useTranslation } from "react-i18next";
import { rtlLanguage } from "common/utils";
import rtlPlugin from "stylis-plugin-rtl";
import { prefixer } from "stylis";
import { CacheProvider } from "@emotion/react";
import createCache from "@emotion/cache";
import { useDispatch, useSelector } from "react-redux";
import { designStateReceived, onDrag, onError } from "design/state/designState";
import LeftPanel from "design/LeftPanel";
import SavingSurvey from "design/SavingSurvey";
import ErrorWrapper from "components/ErrorWrapper";


function EditSurvey() {

  const { i18n } = useTranslation("common");
  const dispatch = useDispatch()



  const headerImage = useSelector((state) => {
    return state.designState["Survey"]?.headerImage
  });

  const groups = useSelector((state) => {
    return state.designState["Survey"]?.children
  });


  const surveyExists = useSelector((state) => {
    return false || state.designState["Survey"]?.instructionList?.length > 0
  });



  const langInfo = useSelector((state) => {
    return state.designState.langInfo
  });

  const lang = langInfo?.lang


  const theme = useSelector((state) => {
    return state.designState["Survey"]?.theme
  });

  const setState = (state) => {
    dispatch(designStateReceived(state))
  }
  const setError = (error) => {
    dispatch(onError(error))
  }


  const ResetDataToServer = () => {
    if (window.confirm(`Are you sure?`)) {
      ResetData(setState, setError)
    }

  }

  useEffect(() => {
    if (isPropsEmpty()) {
      GetData(setState, setError, true).then(data => {
        if (data) {
          console.log(data)
          i18n.loadLanguages(data.languages);
        }

      })
    }
  }, []);

  function isPropsEmpty() {
    console.log(surveyExists)
    return !surveyExists;
  }

  const cacheRtl = createCache({
    key: rtlLanguage.includes(lang) ? "muirtl" : "muiltr",
    stylisPlugins: rtlLanguage.includes(lang)
      ? [prefixer, rtlPlugin]
      : null,
  });

  const surveyTheme = React.useCallback(createTheme({ ...defualtTheme(theme), direction: rtlLanguage.includes(lang) ? "rtl" : "ltr", }), [theme])
  return (
    <CacheProvider value={cacheRtl}>
      <ThemeProvider theme={surveyTheme}>
        <Layout >
          {surveyExists && (
            <>
              <div className={styles.mainContainer} dir={rtlLanguage.includes(lang) ? "rtl" : "ltr"}>
                <DragDropContext
                  onDragEnd={(event) => { onDragEnd(event, (payload) => { dispatch(onDrag(payload)) }) }}
                >
                  < LeftPanel />
                  <ContentPanel
                    headerImage={headerImage}
                    groups={groups} />
                </DragDropContext>
              </div>
              <div className={styles.updateButtons}>
                <Button variant="contained" onClick={() => ResetDataToServer()}>
                  Reset Data
                </Button>
              </div>
              <SavingSurvey />
            </>
          )}
          <ErrorWrapper />
        </Layout>
      </ThemeProvider>
    </CacheProvider>
  );
}

export default EditSurvey;
