import { Box } from "@mui/material";
import styles from "./RandomError.module.scss";


export default function RandomError({
    errors
}) {
    return <Box key="box" className={styles.errorDisplay}>
        {errors.map(error => {
            if (error.name == "PriorityGroupItemNotChild") {
                return <span key={error.name}>
                    Those items are either deleted or have errors and therefore cannot be in Priority Group:&nbsp;
                    <b>
                        {error.items.join(", ")}
                    </b>
                </span>
            }
            else if (error.name == "DuplicatePriorityGroupItems") {
                return <span key={error.name}>
                    Those items are present in more than one priority Group: &nbsp;
                    <b>
                        {error.items.join(", ")}
                    </b>
                </span>
            } else if (error.name == "PriorityLimitMismatch") {
                return "In one of the priority groups, the limit is more than or equal to number of items"
            } else if (error.name == "RandomGroupItemNotChild") {
                return <span key={error.name}>
                    Those items are either deleted or have errors and therefore cannot be in random group: &nbsp;
                    <b>
                        {error.items.join(", ")}
                    </b>
                </span>
            } else if (error.name == "DuplicateRandomGroupItems") {
                return <span key={error.name}>
                    Those items are present in more than one random group: &nbsp;
                    <b>
                        {error.items.join(", ")}
                    </b>
                </span>
            }
        })}
    </Box>
}